:root {
  --primary-color: #052A4F;
}

/***** ABOUT US BODY *****/
#section-about-us {
  display: flex;
  justify-content: center;
  margin: 80px 0;
  height: 100%;
  position: relative;
}

/***** CONTAINER WITH CONTENT *****/
#section-about-us .container {
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 1260px;
}


#section-about-us .container >  h1 {
  font-family: 'Lalezar', cursive;
  color: #021B33;
  font-size: 4.8rem;
  line-height: 5.8rem;
  text-shadow: 1px 1px 3px #000000;
  text-transform: capitalize;
  font-weight: 400;
  
  margin-bottom: 32px;
}

/***** CARDS *****/
#section-about-us .cards {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 80%;
  
  margin: auto;
}


#section-about-us .cards h2 {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.8rem;
  line-height: 2.6rem;
  font-weight: 800;
  text-transform: uppercase;

  color: #052A4F;

  color: var(--primary-color);
}

#section-about-us .cards p,#section-about-us .cards ul  {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.6rem;
  line-height: 2.0rem;
  font-weight: 400;

  color: #052A4F;

  color: var(--primary-color);

  margin: 10px 0px 0px;
}

/***** PRIMARY CARDS (VISION, MISSION AND VALUES) *****/
#section-about-us .cards .primary-cards {
  display: grid;
  grid-template-columns: repeat(3, minmax(192px, 1fr));
  grid-gap: 40px;
  justify-content: center;
}

#section-about-us .cards .primary-cards .container-image  {
  display: flex;
  justify-content: center;
  align-items: center;
}

#section-about-us .cards .primary-cards .container-image .chip-image  {
  width: 100%;
  max-width: 128px;
}

#section-about-us .cards .primary-cards .image {
  position: absolute;
  width: auto;
  max-width: 88px;
  max-height: 78px;
}

#section-about-us .cards .primary-cards, .company-history {
  text-align: center;
}

/***** COMPANY HISTORY *****/

#section-about-us .cards .company-history .company-subtitle {
  display: inline-flex;
}

#section-about-us .cards .company-history .company-subtitle h2 {
  margin: 0 16px;
}

#section-about-us .cards .company-history {
  margin-top: 80px;
}



 /***** MEDIA QUERIES *****/
@media (max-width: 1024px) {
 
  html {
    font-size: 55% !important;
  }

}

@media (max-width: 768px) {

  #section-about-us .container > h1 {
    font-size: 3.2rem;
    line-height: 4.2rem;
