@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

@import url("https://use.fontawesome.com/releases/v5.13.0/css/all.css");

/***** OUR SERVICES BODY *****/
#section-our-services {
  background-color: #021b33;
  background-image: url("../../assets/grafismo-web.png");
  background-repeat: no-repeat;

  background-size: cover;
}

/***** CONTENT CAROUSEL *****/
#section-our-services .container-carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 90%;
  max-width: 1440px;

  margin: auto;
}

#section-our-services .container-carousel h1 {
  font-family: "Lalezar", cursive;
  text-transform: capitalize;
  font-weight: 400;
  color: #fff;
  font-size: 4.8rem;
  line-height: 5.8rem;

  width: 80%;
  display: flex;
  justify-content: flex-end;

  margin-top: 80px;
  margin-bottom: 16px;
}

/***** SLICK BODY STYLES *****/
#section-our-services .slick-slider {
  width: 90% !important;
  margin-bottom: 80px;
}

#section-our-services .slick-slide > div {
  width: 95%;
  margin: auto;
}

/***** CAROUSEL CARD *****/
#section-our-services .container-carousel .card {
  display: grid !important;
  grid-template-columns: 2fr 1fr;
  align-self: center;
  gap: 16px;

  height: 100%;
  width: 100%;

  min-height: 500px;

  margin-bottom: 32px;
}

#section-our-services .container-carousel .card h3 {
  font-size: 2.4rem;
  line-height: 3.4rem;
  font-weight: 900;
  color: #052a4f;
}

#section-our-services .container-carousel .card .primary-card h3 {
  margin: 12px 0;
}

#section-our-services .container-carousel .card p {
  font-size: 2rem;
  line-height: 2.8rem;
  font-weight: 400;
  color: #052a4f;
}

/***** PRIMARY CARD *****/
#section-our-services .container-carousel .card .primary-card {
  background: url("../../assets/primary-layer.svg") no-repeat;
  background-size: cover;

  height: 100%;
  max-height: 500px;

  border-radius: 12px;
}

#section-our-services .container-carousel .card .primary-card .content {
  position: relative;

  display: grid;
  grid-template-rows: 1.5fr 1fr;
  height: 100%;

  overflow-y: auto;
  scroll-behavior: smooth;

  border-radius: 12px;
}

#section-our-services
  .container-carousel
  .card
  .primary-card
  .content
  .primary-card-image {
  width: 100%;
  z-index: -1;
  position: absolute;
  top: -70px;
}

#section-our-services .container-carousel .card .primary-card .description {
  position: absolute;
  top: 55%;
  background-color: #f2e463;

  padding: 0 32px 32px 32px;
  border-radius: 0 0 16px 16px;
}

/***** SECONDARY CARD *****/
#section-our-services .container-carousel .card .secondary-card {
  display: grid;
  grid-template-rows: minmax(1fr, 250px);
  gap: 16px;
  height: 100%;
  overflow: hidden;
}

#section-our-services .container-carousel .card .secondary-card .box-before,
.box-after {
  background-image: url("../../assets/secondary-layer.svg");
  background-repeat: no-repeat;
  background-size: cover;

  height: 100%;

  border-radius: 12px;
}

#section-our-services .container-carousel .card .secondary-card .content {
  display: grid;
  grid-template-rows: 1.8fr 1fr;
  height: 100%;
  position: relative;
  overflow: hidden;
}

#section-our-services .container-carousel .card .secondary-card .before-image,
.after-image {
  width: 100%;
  height: 100%;
  z-index: -1;
}

#section-our-services .container-carousel .card .secondary-card .description {
  height: 100%;
  padding: 0 16px 16px 16px;
  background-color: #f2e463;
  border-radius: 0 0 16px 16px;
}

/***** SLICK DOTS AND ARROWS STYLES *****/
#section-our-services .slick-dots li {
  margin: 0 8px;
}

#section-our-services .slick-dots li button:before {
  font-size: 28px;
  color: #d4d4d4;
  opacity: 1;
}

#section-our-services .slick-dots li.slick-active button:before {
  opacity: 1;
  color: #f2e463 !important;
}

#section-our-services .slick-next {
  right: -20px;
}

#section-our-services .slick-prev {
  left: -20px;
}

#section-our-services .slick-prev,
.slick-next {
  top: 45%;
}

#section-our-services .slick-prev:before {
  content: "\f053";
}

#section-our-services .slick-next:before {
  content: "\f054";
}

#section-our-services .slick-prev:before,
.slick-next:before {
  font-family: "Font Awesome 5 Free";
  font-size: 40px;
  opacity: 1;
  color: #d4d4d4;
  font-weight: 900;
}

/***** MOD SCROLLBAR ******/
#section-our-services
  .container-carousel
  .card
  .primary-card
  .content::-webkit-scrollbar {
  display: none;
}

/***** MEDIA QUERIES *****/
@media (max-width: 1024px) {
  html {
    font-size: 55%;
  }

  #section-our-services {
    background-color: #021b33;
    background-image: url("../../assets/grafismo-mobile.png");
    background-position: center;
    background-size: cover;
  }

  #section-our-services .container-carousel > h1 {
    justify-content: flex-start;
    margin-right: 12%;
    z-index: 2;
  }

  #section-our-services .container-carousel .card {
    display: grid !important;
    grid-template-columns: 1fr;
    gap: 16px;

    width: 100%;
  }

  #section-our-services .container-carousel .card .primary-card {
    box-shadow: 0px 3px 6px #000000;
  }

  #section-our-services .container-carousel .card .secondary-card {
    display: none;
  }
}

@media (max-width: 768px) {
  #section-our-services .container-carousel > h1 {
    font-size: 3.2rem;
    line-height: 4.2rem;

    margin-bottom: 24px;
  }

  #section-our-services .container-carousel .card {
    display: grid !important;
    grid-template-columns: 1fr;
    gap: 16px;

    width: 100%;
  }

  #section-our-services .container-carousel .card h3 {
    font-size: 2.2rem;
    line-height: 3.2rem;
    font-weight: 900;
    color: #052a4f;
  }

  #section-our-services .container-carousel .card p {
    font-size: 1.8;
    line-height: 2.8rem;
    font-weight: 400;
    color: #052a4f;
  }

  #section-our-services .container-carousel .card .primary-card {
    box-shadow: 0px 3px 6px #000000;
  }

  #section-our-services .container-carousel .card .secondary-card {
    display: none;
  }

  #section-our-services .slick-dots li {
    margin: 0 4px;
  }

  #section-our-services .slick-dots li button:before {
    font-size: 18px;
  }

  #section-our-services .slick-prev:before,
  .slick-next:before {
    font-family: "Font Awesome 5 Free";
    font-size: 28px;
    font-weight: 900;
  }
}

@media (max-width: 560px) {
  #section-our-services .container-carousel .card h3 {
    font-size: 2.2rem;
    line-height: 3rem;
    font-weight: 900;
    color: #052a4f;
  }

  #section-our-services .container-carousel .card p {
    font-size: 1.6;
    line-height: 2.6rem;
    font-weight: 400;
    color: #052a4f;
  }

  #section-our-services .container-carousel .card {
    display: grid !important;
    grid-template-columns: 1fr;

    min-height: 400px;
    width: 100%;
  }

  #section-our-services .container-carousel .card .primary-card {
    max-height: 400px;
  }

  #section-our-services .container-carousel .card .primary-card .content {
    display: grid;
    grid-template-rows: 1.2fr 1fr;
    height: 100%;
  }

  #section-our-services .container-carousel .card .primary-card .description {
    padding: 0 16px 16px 16px;
    text-align: center;
  }

  #section-our-services .container-carousel .card .secondary-card {
    display: none;
  }

  #section-our-services .slick-dots li {
    margin: 0 4px;
  }
}
